.draggable-divider-container {
    position: relative;
    display: flex;
    flex-direction: row; /*  'column' */
    width: 100%;
    height: 100%;
  }
  
  .draggable-divider {
    background-color: #ccc;
    cursor: col-resize; /*  'row-resize' */
    transition: background-color 0.3s;
  }
  
  .draggable-divider.dragging {
    background-color: #aaa;
  }
  